<template>
  <div id="email">
    <!-- карта -->
    <div class="row text-center">
              <div class="col-md-6">
                <iframe
                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d34237.37782275726!2d75.4444827979007!3d63.19258216135913!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x436fa2ce334363b7%3A0xff8a2e7f814d6722!2z0J3QvtGP0LHRgNGM0YHQuiwg0K_QvNCw0LvQvi3QndC10L3QtdGG0LrQuNC5INCw0LLRgtC-0L3QvtC80L3Ri9C5INC-0LrRgNGD0LM!5e0!3m2!1sru!2sru!4v1592478840125!5m2!1sru!2sru"
                    width="380" height="380" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false"
                    tabindex="0">
                </iframe>
        </div>
        <!-- Форма обратной связи -->
      <div class="col-md-4">
        <div>
          <h3>Обратная связь</h3>
            <a id="Email"></a>
          <div class="input-group mb-3">
            <span class="input-group-text">Имя</span>
            <input
              id="dname"
              type="text"
              class="form-control"
            />
          </div>
          <div class="input-group mb-3">
          <span class="input-group-text">email</span>
          <input
          id="demail"
          type="text"
          class="form-control"
          />
          <span class="input-group-text">@example.com</span>
          </div>
          <select class="form-select" aria-label="Default select example">
            <option selected>Благодарность</option>
            <option value="1">Ошибка</option>
            <option value="2">Письмо директору</option>
            <option value="3">Жалоба</option>
          </select>
          <p></p>
          <div >
            <label class="form-check-label" for="flexCheckIndeterminate">Согласие на обработку персональных данных</label>&nbsp;
            <input class="form-check-input" type="checkbox" value id="flexCheckIndeterminate"/>      
          </div>
          <p></p>
          <div class="input-group">
            <span class="input-group-text">Текст сообщения</span>
            <textarea class="form-control" aria-label="With textarea"></textarea>
            <button @click="sendform" class="btn btn-info">Отправить</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Email",

  methods: {
// валидация формы
   sendform() {
var dn = document.getElementById("dname");
var em = document.getElementById("demail");
var ch = document.getElementById("flexCheckIndeterminate");

if(dn.value == ""){
  alert('Пожалуйста, введите Ваше имя');
  return false;
}else if (em.value == "") {
    alert('Пожалуйста, введите электронный адрес');
    return false;
}
else if(ch.checked == false){
alert('Предоставьте согласие на обработку персональных данных');
return false;
  }
alert('Письмо отправлено!!!');
return true;
    }
  }
};
</script>

<style scoped>
#email {
  margin: 40px;
}
.col-md-4{
  margin: 20px;
}
</style>